@media only screen and (max-width:1000px)  {
   
    #root{
        
        overflow-x: hidden;
    
    }



    .App{
        position: relative;
    }

    nav{
        position: fixed;
        width: 50%;
        height: 300px;
        top: -500px;
        background-color: black;
        transition: .5s ease-in;
    }

    nav ul{
        flex-direction: column;
        align-items: center;
        font-size: 2rem;
    }

    .navShow{
        position:fixed;
        width: 100%;
        height: 300px;
        top: -100px;
        background-color: black;
        transition: .5s ease-in;
        z-index: 4;
    }

    .Burger{
        width: 50px;
        height: 50px;
        position: fixed;
        display: flex;
        flex-direction: column;
        justify-content:space-around;
        align-items: center;
        top: 2%;
        right: 10%;
        z-index: 5;
    }

    .burgerBar{
        width: 60px;
        height: 5px;
        background-color: red;
        border-radius: 15px;
        transition: .5s ease-in;
        z-index: 1;
    }

    .firstBarOn{
        transform: translate(0px,15px) rotate(45deg);
        transition: .5s ease-in;
    }

    .secoundBarOn{
        background-color: transparent;
        transition: .5s ease-in;
    }

    .thirdBarOn{
        transform: translate(0px,-17.5px) rotate(-45deg)  ;
        transition: .5s ease-in;
    }


    aside{
        display: flex;
        top: 100px;
    }

    .orderInfo{
        flex-wrap: wrap;
        height: 500px;
        width: 90%;
        padding: 10px;
    }

    .orderInfoBox{
        width: 150px;
        height: 200px;
    }

    .Start{
        height: 100vh;
        width: 90%;
        z-index: 1;
    }

    .wrapperStart{
        width: 100%;
        height: 500px;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }

    .startingSet{
        width: 100px;
    }

    .Pro{height: 25vh;}

    .Start,.Pro h2{text-align: center;}

    

    .starterBox{
        width: 200px;
        height: 250px;
    }

    .wrapperPro{
        width: 100%;
        height: 1000px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
    }

    .proSet{
        width: 120px;
    }

    .proBox{
        width: 150px;
        height: 200px;
    }
    .productBtn{
        z-index: 2;
    }


    .accountContainer{
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .logInBox,.registerBox{
        width: 100%;
    }
        

}