@media only screen and (max-width:500px)  {
   
    
   

    aside{
        right: -290px;
    }
   
    .orderInfo{
        flex-wrap: wrap;
        height: 500px;
        width: 80%;
        padding: 10px;
    }

    .orderInfoBox{
        width: 100px;
        height: 150px;
    }
    .orderInfoBox p{
        font-size: 20px;
    }

    .Start{
        height: 100vh;
        width: 90%;
        z-index: 1;
    }

    .wrapperStart{
        width: 100%;
        height: 500px;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }

    .startingSet{
        width: 100px;
    }

    .Pro{height: 200vh;}

    .Start,.Pro h2{text-align: center;}

    

    .startingBox{
        width: 150px;
        height: 200px;
    }

    .wrapperPro{
        width: 100%;
        height: 1000px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
    }

    .proSet{
        width: 120px;
    }

    .proBox{
        width: 150px;
        height: 200px;
    }
    .productBtn{
        z-index: 2;
    }

    .accountContainer{
        height: 150vh;
    }


    .basketIcon{
        width: 5rem;
        height: 5rem;
        margin-left: 40px;
    }
        

}