*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family:'Times New Roman', Times, serif;
    color: whitesmoke;
}

body{
    overflow-x: hidden;
    background-image: url(./components/images/background.png);
    background-repeat: no-repeat;
    background-size: cover;
}

#root{
    height: 100%;

}

.App{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
}


header{
    width: 100%;
    height: 3em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 3em;
}

li{
    list-style: none;
}



header li a {
    text-decoration: none;
}

header p:nth-child(2){
    color: red;
}

.Burger{
    display: none;
}

nav{
    width: 80%;
    height: 100%;
    font-size: 1.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 100px;
}

nav ul{
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
    height: 100%;
}


nav li a {
    text-decoration: none;
    border: 1px solid red;
    width: 150px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    
}
nav li a:hover {
    color: black;
    background-color: red;
}



.orderInfo{
    width: 90%;
    height: 250px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 100px;
}

.orderInfoBox{
    width: 200px;
    height: 250px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    border-radius: 15px;
}

.orderInfoBox p {
    color: black;
    font-size: 2rem;
}

svg{
    fill: black;
    z-index: 5;
    width: 4em;
    height: 4em;
}

aside{
    display: flex;
    justify-content: space-between;
    position: fixed;
    width: 25em;
    height: 100%;
    top: 0;
    right: -260px;
    z-index: 5;
    transition: 1s ease-in;
}
.asideContainerOn{
    display: flex;
    justify-content: space-between;
    position: fixed;
    width: 25em;
    height: 100%;
    top: 0;
    right: 0;
    z-index: 5;
    
}

.productsCount{
    color: red;
    width: 20px;
    height: 20px;
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: whitesmoke;
    border-radius: 15px 0 15px 15px;
    text-align: center;
    font-size: 1rem;
}

.basketIcon{
    display:flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items:center;
    width:6rem;
    height: 6rem;
    border-radius: 50%;
    background-color: whitesmoke;
    cursor: pointer;
    margin-top: 10px;
    position: relative;

}

.basket{
    display: flex;
    text-align: center;
    flex-direction: column;
    width: 65%;
    height: 1500px;
    gap: 10px;
    padding: 20px 0;
    background-color: black;
    overflow-y: scroll;
}

.basketBox{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 20px 0;
    text-align: center;
    min-height: 70px;
    width: 100%;
}

.basketBox button{
    border-radius: 15%;
    border: 3px red solid;
    background-color: black;
    color: whitesmoke;
    text-align: center;
    width: 30px;
    height: 30px;
}
.btnText{
    display: flex;
    justify-content: space-around;
    width: 100%;
    
}

main{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 25px;
}

main button{
    color: whitesmoke;
}

.orderImg img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
}

.priseSet{
    color:whitesmoke;
    font-size: 4rem;
}

.Start{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 100px;
}

.wrapperStart{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 30%;
    justify-content:center;
    align-items: center;
}
.wrapperPro{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 30%;
    justify-content:space-around;
    align-items: center;
}

.Start button{
    width: 10rem;
    height: 5rem;
    border-radius: 15px;
    background-color: transparent;
    border: 2px solid red;
    font-size: 32px;
}

.Start button:hover{
    background-color: whitesmoke;
    color: red;
    border: black 2px solid;
}

.startingSet{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 20%;
    height: 250px;
}

.startingBox{
    width: 200px;
    height: 250px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content:flex-end ;
    align-items: center;
    text-align: center;
    border-radius: 15px;
    position: relative;
}

.startingBox .nameSet{
    color:whitesmoke;
    padding-bottom: 10px;
    z-index: 3;
}

.startingBox .orderImg{
    width: 100%;
    height: 100%;
    position: absolute;
}

/* Pro set */

.Pro{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 100px;
}

.Pro button{
    width: 10rem;
    height: 5rem;
    border-radius: 15px;
    background-color: transparent;
    border: 2px solid red;
    font-size: 32px;
}

.Pro button:hover{
    background-color: whitesmoke;
    color: red;
    border: black 2px solid;
}

.proSet{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 10%;
    height: 250px;
    flex-wrap: wrap;
    gap: 50px;
}

.proBox{
    width: 200px;
    height: 250px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content:flex-end ;
    align-items: center;
    text-align: center;
    border-radius: 15px;
    position: relative;
}

.proBox .orderImg{
    width: 100%;
    height: 100%;
    position: absolute;
}

.openingHours{
    height: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.openingHours ul{
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-size: 1.5rem;
    list-style: none;
}

.shopStatus{
    font-size: 2rem;
    margin-bottom: 10px;
    text-decoration: underline gold;
}

.Gradient{
    top:0;
    width: 100%;
    height: 100%;
    position: absolute;
    background: linear-gradient(0deg, rgb(0, 0, 0, 1) 0%, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 0) 100%); 
    z-index: 2;
    transition: 5s ease-in;
}



.contactContainer{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.Contact ul{
    width: 90vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    gap: 50px;
}

.Contact ul li{
    width: 150px;
    height: 200px;
    background-color: whitesmoke;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.Contact li:nth-child(1){
    background-color: royalblue;
}

.Contact li:nth-child(2){
    background: linear-gradient(50deg, rgb(191, 255, 0) 0%, rgb(255, 72, 0) 30%, rgb(255, 0, 208) 100%);;
}


.Contact li p{
    color: black;

}

.returnPolicy{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 50vh;
}

.returnPolicy p{
    width: 300px;
}

.returnPolicy ul{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    list-style: none;
    width: 300px;
    height: 150px;
}

.returnPolicy ul li{
    text-decoration: underline red 3px;
    font-size: 20px;
}

.Information{
    width: 80%;
    height: 600px;
    text-align: center;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

/* Products */

.productBox img{
    width: 100%;
    height: 100%;
}

.productBox{
    width: 200px;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
    border-radius: 15px;
    position: relative;
}

.productImage{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}

.productName,.productPrise{
    color: whitesmoke;
    z-index: 2;
    width: 80%;
    margin-bottom: 10px;
}

.productContainer{
    height:300px ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.productBtn{
    z-index: 4;
    width: 75px;
    height: 25px;
    color: black;
    border-radius: 5px;
}

.shopSets{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.searchBar{
    margin: 50px;
}

.shopResult{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
}

.searchBar input{
    width: 400px;
    height: 50px;
    border-radius: 15px;
    color: black;
    font-size: 2rem;
    text-align: center;
}

.accountContainer{
    width: 90vw;
    height: 80vh;
    display: flex;
    justify-content: space-around;
    border-radius: 15px;
}
.logInBox,.registerBox{
    width: 45%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: whitesmoke;
    border-radius: 15px;
    background-color: black;
}

form{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 50px;
    background-color: whitesmoke;
}

form label{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 200px;
    height: 75px;
}

form input{
    border: 1px solid red;
    border-radius: 50px;
    background-color: transparent;
    width: 200px;
    height: 50px;
    text-align: center;
}



.accountContainer button{
    width: 150px;
    height: 50px;
    border: whitesmoke 1px solid;
    border-radius: 50px;
    background-color: red; 
    color: whitesmoke; 
}

.accountContainer button:hover{
    background-color: whitesmoke;
    border: red 1px solid;
    color: black;
    cursor: pointer;
}

form label,button,input,.creatingAccount,.forgetAccount,.regBtn,.logBtn{
    color: black;
}

.forgetAccount:hover{
    text-decoration: underline 3px solid red;
    cursor: pointer;
}

.accountBox{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}